import React from 'react';
import { BrowserRouter, Switch, Route, Redirect, NavLink } from "react-router-dom"
import './App.scss'
import logo from './logo.png';
import 'antd/dist/antd.css';
import Home from "./page/home"
import Button from "./page/button"
import Container from "./page/container"
import Image from "./page/image"
import List from "./page/list"
import Nav from "./page/nav"
import Slider from "./page/slider"
import Text from "./page/text"
import Icon from "./page/icon"

function App() {

  return (
    <div className="App">
      <BrowserRouter>
        <div className="App-header"> 
          <div className="logo"><img src='https://nwzimg.clouddream.net/picture/Rr5ri-lRMkmO6yp2AnWuVw/W4ktuFRpyUKV52yoWVXVig.png' alt="logo"/></div>
          <header className="header">
            <NavLink to="/hover_effct/home">Home</NavLink>
            <NavLink to="/hover_effct/button">Button</NavLink>
            <NavLink to="/hover_effct/container">Container</NavLink>
            <NavLink to="/hover_effct/image">Image</NavLink>
            <NavLink to="/hover_effct/list">List</NavLink>
            <NavLink to="/hover_effct/nav">Nav</NavLink>
            <NavLink to="/hover_effct/slider">Slider</NavLink>
            <NavLink to="/hover_effct/text">Text</NavLink>
            <NavLink to="/hover_effct/icon">Icon</NavLink>
          </header>
        </div>
        <div className="App-container">
        <Switch>
          <Redirect from="/hover_effct/" exact to="/home"></Redirect>
          <Route path="/hover_effct/home" component={Home} ></Route>
          <Route path="/hover_effct/button" component={Button} ></Route>
          <Route path="/hover_effct/container" component={Container} ></Route>
          <Route path="/hover_effct/image"  component={Image} ></Route>
          <Route path="/hover_effct/list"  component={List} ></Route>
          <Route path="/hover_effct/nav"  component={Nav} ></Route>
          <Route path="/hover_effct/slider"  component={Slider} ></Route>
          <Route path="/hover_effct/text" component={Text} ></Route>
          <Route path="/hover_effct/icon" component={Icon} ></Route>
        </Switch>
        </div>
      </BrowserRouter>
    </div>
  );
}

export default App;